.ContentPage_BG{
    filter: blur(5px);

    position: fixed;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    transition: all .01s;

    overflow: scroll;
}

.ContentPage_Fitter{
    position: absolute;

    left: 0px;
    top: 100px;
    width: 100%;

    padding-bottom: 50px;
    box-sizing: border-box;
}

.ContentPage_Container{
    margin: auto;

    width: calc(1920px * .6);

    padding: 20px;
    padding-top: calc((1920px * .65) * (1080 / 1920));
}




div.ContentPage_Header{
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;

    padding: 5px;
    box-sizing: border-box;
}

img.ContentPage_Header{
    width: 100%;
    margin: auto;

    border-radius: 20px;

    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 100%);
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 0) 100%); /* For Safari */
}

h1.ContentPage_Header{
    position: absolute;

    top: 25px;
    left: 0px;
    right: 0px;

    margin: auto;
    width: 90%;

    background: #000000;
    background: linear-gradient(90deg,rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, 1) 90%, rgba(0, 0, 0, 0) 100%);

    padding: 10px;

    text-align: center;
    box-sizing: border-box;

    z-index: 100;
}





.ContentPage_Content{
    width: 100%;

    display: flex;
    justify-content: space-between;
    gap: 50px;
}

.ContentPage_Images{
    flex-grow: 0;

    width: 400px;
    
    display: flex;
    flex-grow: column;

    flex-direction: column;
    gap: 10px;
}

.ContentPage_Image{
    width: 100%;
    border-radius: 10px;
}

.ContentPage_ShowAllImages{
    position: relative;
}

img.ContentPage_ShowAllImages{
    filter: blur(5px);
    cursor: pointer;
}

span.ContentPage_ShowAllImages{
    position: absolute;

    left: 0px;
    right: 0px;

    text-align: center;
    top: 50%;
    font-size: 25px;

    cursor: pointer;
}



.ContentPage_Entries{
    order: -1;
    flex-grow: 1;
    width: 100%;
}

.ContentPage_Entries > pre{
    margin-bottom: 15px;
    text-align: center;
    text-wrap-mode: wrap;
}

.ContentPage_Entries_Header{
    margin: auto;
    width: 100%;

    background: #000000;
    background: linear-gradient(90deg,rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, 1) 90%, rgba(0, 0, 0, 0) 100%);

    padding: 10px;

    text-align: center;
    box-sizing: border-box;

    margin-bottom: 25px;
}

.ContentPage_Entries_Tags{
    width: 100%;

    display: flex;
    gap: 10px;

    justify-content: center;

    margin-bottom: 25px;
}

.ContentPage_Entries_Tags > div{
    background-color: rgb(50, 50, 50);
    padding: 10px;

    padding-left: 15px;
    padding-right: 15px;
    border-radius: 20px;

    font-size: 12px;
}